<template>
	<div id="storagepie" style="height:380px">
		
	</div>
</template>
<script>
	export default{
		props:['consumptionObj'],
		data(){
			return{
				pieArr:[],
			}
		},
		mounted(){
			this.pieFunction()
		},
		watch: {
			used(value){
				this.used = value				
			},
			free(value){
				this.free = value
			}
		},
		methods:{
			pieFunction(){
      	this.pieArr = [
			{'name':this.$t('dashboard.eat'),'value':this.consumptionObj.eatNum},
			{'name':this.$t('dashboard.shopping'),'value':this.consumptionObj.buyNum},
			{'name':this.$t('dashboard.houserent'),'value':this.consumptionObj.houserentNum},
			{'name':this.$t('dashboard.traffic'),'value':this.consumptionObj.trafficNum},
			{'name':this.$t('dashboard.communication'),'value':this.consumptionObj.communicationNum},
			{'name':this.$t('dashboard.others'),'value':this.consumptionObj.othersNum},
		];
				var myPie = this.$echarts.init(document.getElementById('storagepie'),'macarons');
				// 
	      var option = {
		    	title : {
		        text: this.$t('dashboard.pietitle'),
		        subtext: this.$t('dashboard.totalconsumption')+  this.consumptionObj.totalcommunicationNum,
		        x:'center',
		        textStyle:{
		// 		fontSize:14
		    		}
			    },
			    tooltip : {
		        trigger: 'item',
		        formatter: "{a} <br/>{b} : {c} ({d}%)"
			    },
			    legend: {
		        orient : 'vertical',
		        left: 20,
        		top: 20,
		        // data:this.nameArr
			    },
					toolbox: {
						itemSize:20,
					  feature: {
					      saveAsImage: {
					      	show:true,
                	title:this.$t('dashboard.savepic'),
                	icon: 'image://'+require("../../assets/images/picture.png"),
					      }
					  }
					},
			    calculable : true,
			    series : [
	        	{
	            name:this.$t('dashboard.percent'),
	            type:'pie',
	            radius : '55%',
	            center: ['50%', '60%'],
	            data:this.pieArr,
	            itemStyle:{ 
		            normal:{ 
                  label:{ 
                    show: true, 
                    formatter:'{c}{b} ({d}%)' 
                  }, 
		                labelLine :{show:true} 
		            } 
		        	} 
						}
			    ],				    
				};
        myPie.setOption(option);
        window.addEventListener("resize", function () {
        	myPie.resize();
      	});
			}
		}

}
</script>