<script>
import VStoragepie from './storagepie'//

import VInandout from './inandout'///
// import { exportMateInfo, report } from '@/request/api/material'
import { genUrl, localDate, Debounce,downloadExcel } from '@/utils/public'
export default{
	components:{VStoragepie,VInandout},
	data(){
		return{
			consumptionObj:{
				"totalcommunicationNum":6728,
				"eatNum":2400,
				"shoppingNum":660,
				"houserentNum":2200,
				"trafficNum":180,
				"communicationNum":88,
				"othersNum":1200,
			},
			mate_in_count:[20,43,67,12,0,77,33,66,77,12,40,33,55,0,25,47,39,4,69,33,47,6,18,200,47,80,67,54,36,92],//
			mate_out_count:[10,13,47,56,66,37,53,66,27,90,107,73,45,71,25,47,69,44,91,53,56,62,59,270,37,46,77,67,66,42],//

		}
	},
	mounted() {
		// this.getReport()
	},
	methods:{
		getReport() {
			report({}).then(res =>{
				if (res.result) {				
					this.mate_in_count = res.mate_in_count
					this.mate_out_count = res.mate_out_count
				} else {
					this.$message({
            showClose: true,
            message: res.msg,
            type: 'error'
          })
				}
			})
		},
		downLoad:Debounce(function(){
			this.$http({
				url: '/mate/exportMateInfo',
				method: 'post',
				data: {
			  	user_name: localStorage.getItem('user_name'),
			  	user_group_power: localStorage.getItem('user_group_power')
				},
				headers:{charset:"utf-8"},
				responseType:'blob'
			}).then(res=>{
				let blob = new Blob([res.data],{type: 'application/vnd.ms-excel'});
				let temp = res.headers["content-disposition"].split(";")[1].split("filename=")[1];
				let iconv = require('iconv-lite'); 
		    iconv.skipDecodeWarning = true;//
		 		let fileName = iconv.decode(temp, 'utf-8').replace(/\"/g, "");
		 		const link = document.createElement('a')
		 		link.style.display = 'none';
				link.href = URL.createObjectURL(blob);
				link.download = fileName;
				link.click();
 				window.URL.revokeObjectURL(link.href);
			})
		}),
		exportMateInOut:Debounce(function(){
			this.$refs.mateinout.exportData()
		})	
	}
}
</script>
