<template>
	<div id="inandoutline" style="height:380px"></div>
</template>
<script>
	import { Debounce } from '@/utils/public'
	export default{
		props:{
			incount:{
				type:Array,
				required:true
			},
			outcount:{
				type:Array,
				required:true
			}
		},
		data() {
			return{
				log_type:'all',
				monthdays:[],
			}
		},
		created(){
			this.incount.forEach((item,index,arr)=>{
				this.monthdays.push(index+1)
				// console.log(this.monthdays)
			})
		},
		mounted(){
			this.lineFunction()
			
		},
		watch:{
			incount:{
				handler(value){
				this.incount = value	
			},
			deep:true
			},
			outcount:{
				handler(value){
					this.outcount = value
				},
				deep:true
			},
			log_type:{
				handler(value){
					this.log_type = value
				}
				
			}

		},
		methods:{
			lineFunction(){
				let _this = this
				var myLine = this.$echarts.init(document.getElementById('inandoutline'),'macarons');
				var option = {
					title: {
					  text: this.$t('dashboard.linetitle'),
					  x:'center'
					},
					tooltip: {
					  trigger: 'axis'
					},
					legend: {
						orient : 'vertical',
		        left: 15,
        		top: 10,
					  // data: ['', '']
					},
					grid: {
					  left: '3%',
					  right: '4%',
					  bottom: '3%',
					  containLabel: true
					},
					toolbox: {
						itemSize:20,
						 dataView: { //
               show: true
            },
            restore: { //
              show: true
            },
					  feature: {
					  	myExport: { //
					  		show:true,
                title:this.$t('dashboard.datatable'),
                icon: 'image://'+require("../../assets/images/export.png"),
                onclick:function(){
                	_this.exportData()
                }
              },
					     saveAsImage: {
					     	show:true,
                title:this.$t('dashboard.savepic'),
                icon: 'image://'+require("../../assets/images/picture.png"),
					     }				    		      
					  }
					},
					xAxis: {
					  type: 'category',
					  axisLabel:{interval: 0},
					  boundaryGap: false,
					  data: this.monthdays
					},
					yAxis: {
					  type: 'value'
					},
					series: [
					  {
					      name: this.$t('dashboard.in'),
					      type: 'line',
					      data: this.incount
					  },
					  {
					      name: this.$t('dashboard.out'),
					      type: 'line',
					      data: this.outcount
					  },
					]
				};
				myLine.setOption(option,true)
        window.addEventListener("resize", function () {
        	myLine.resize();
      	});
      	myLine.on('legendselectchanged',function(params){
      		let feedering = params.selected. || params.selected.Feeding
      		let taking = params.selected. || params.selected.Taking
      		if(feedering && taking){
      			this.log_type = 'all'
      		}else if(feedering){
      			this.log_type = 'in'
      		}else if(taking){
      			this.log_type = 'out'
      		}else{

      		}
      		localStorage.setItem('log_type',this.log_type)
      	});      	
			},
			exportData:Debounce(function(){
				this.$http({
					url: '/export/exportMateInOutRecords',
					method: 'post',
					data: {
				  	user_name: localStorage.getItem('user_name'),
				  	user_group_power: localStorage.getItem('user_group_power'),
				  	log_type:localStorage.getItem('log_type')
					},
					headers:{charset:"utf-8"},
					responseType:'blob'
				}).then(res=>{
					let blob = new Blob([res.data],{type: 'application/vnd.ms-excel'});
					let temp = res.headers["content-disposition"].split(";")[1].split("filename=")[1];
					let iconv = require('iconv-lite'); 
			    iconv.skipDecodeWarning = true;//
			 		let fileName = iconv.decode(temp, 'utf-8').replace(/\"/g, "");
			 		const link = document.createElement('a')
			 		link.style.display = 'none';
					link.href = URL.createObjectURL(blob);
					link.download = fileName;
					link.click();
	 				window.URL.revokeObjectURL(link.href);
				})
			}),
				
			
		}
	}
</script>
